<template lang="html">

  <canvas id="background-canvas" class="background"></canvas>

</template>

<script lang="js">
  import circle from '@/assets/img/background/circle.png'
  import square from '@/assets/img/background/square.png'
  import triangle from '@/assets/img/background/triangle.png'
  import TweenMax from "gsap/TweenMax"

  export default  {
    name: 'background',
    props: [],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {

    },
    mounted: () => {
      var can = document.getElementById('background-canvas')
      var ctx = can.getContext('2d')

      let size = window.innerHeight / 1.5
      let demiSize = size/2

      let deg = 0

      window.addEventListener("resize", resizeCanvas)
      resizeCanvas()

      window.mobileAndTabletcheck = function() {
      var check = false;
      (function(a){if(/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino|android|ipad|playbook|silk/i.test(a)||/1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0,4))) check = true;})(navigator.userAgent||navigator.vendor||window.opera);
      return check;
    };

      function resizeCanvas(){
          can.style.height = window.innerHeight + "px"
          can.style.width = window.innerWidth + "px"
          can.height = window.innerHeight
          can.width = window.innerWidth
      }

      class Form{
        constructor(form) {
          this.x = Math.floor(Math.random() * (window.innerWidth));
          this.y = Math.floor(Math.random() * (window.innerHeight ));
          if(this.x < demiSize) this.x = 0
          if(this.y < demiSize) this.y = 0
          this.size = demiSize
          this.opacity = 0
          this.rotation = 0
          this.animated = false
          this.img = new Image()
          switch (form) {
            case 0:
              this.img.src = square
            break;

            case 1:
              this.img.src = circle
              
            break;

            case 2:
              this.img.src = triangle
            break;
          
            default:
              this.img.src = circle
              break;
          }
        }

        update(){
          
          this.draw()
        }

        draw(){
          ctx.save()
          ctx.globalAlpha = this.opacity
          ctx.translate(this.x, this.y)
          ctx.rotate(this.rotation * Math.PI / 180)
          ctx.drawImage(this.img, -demiSize, -demiSize,this.size,this.size)
          ctx.restore()
        }
      }

      let time = 0
      let now = Date.now()
      let past = now
      let deltaTime = 0
      if (!window.mobileAndTabletcheck()) {
        requestAnimationFrame(animation)
      }

      let forms = []
      function animation(){
          now = Date.now()
          deltaTime = now - past
          past = now
          time += deltaTime

          if (time >= 500) {
            time = 0
            forms.push(new Form(Math.floor(Math.random() * Math.floor(3))))
          }

        ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);
        requestAnimationFrame(animation)

        forms.forEach(form => {
          if (form.animated == false) {
            TweenMax.to(form,4,{size:size, rotation: 25})
            TweenMax.to(form,2,{opacity: 1, onComplete: ()=>{
              TweenMax.to(form,1,{opacity: 0, onComplete: ()=>{
                forms.splice(forms.indexOf(form), 1);
              }})
            }})
            form.animated = true
          }
          form.draw()
        })

        /*deg += 1
        if (deg >= 360) {
          deg = 0
        }*/

        //can.style.filter = 'blur(40px) hue-rotate('+deg+'deg)'


        
      }

          forms.push(new Form(Math.floor(Math.random() * Math.floor(3))))  
      
    }
}
</script>

<style scoped>
  .background {

  }
  #background-canvas{
    position: fixed;
    z-index: 1;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;

    transition: all 0.3s ease;
  }
</style>
